import React, { useCallback, useState } from 'react';
import styled from 'styled-components';
import { useDispatch, useSelector } from 'react-redux';
import {
  Input, Button, Table, TableRow,
} from '../components';
// http://api.weatherstack.com/current?access_key=c18c9cd0a7159022db85263eea437534&query=New%20York
import { getWeatherRequest } from './actions';

export function Weather() {
  const [value, setValue] = useState('');
  const dispatch = useDispatch();

  const getWeather = useCallback(() => {
    dispatch(getWeatherRequest(value));
  }, [dispatch, value]);

  const { loading, data, errorMessage } = useSelector((state) => state.weather);

  return (
    <Container>
      <form onSubmit={(e) => e.preventDefault()}>
        <Input value={value} onChangeText={setValue} />
        <Button onClick={getWeather}>{loading ? 'Loading...' : 'Find' }</Button>
      </form>
      {errorMessage ? <h1 style={{ color: 'red' }}>{errorMessage}</h1> : null }
      <Table>
        {
        data.map((row) => <TableRow data={row} />)
      }
      </Table>
    </Container>
  );
}

const Container = styled.div`
 display: flex;
 justify-content: center;
 align-items: center;
 flex-direction: column;

`;
