import React, { useMemo } from 'react';

import styled from 'styled-components';
import { useSelector } from 'react-redux';
import { useParams, useHistory } from 'react-router-dom';

export const DetailsPage = () => {
  const {
    data,
  } = useSelector((state) => state.recipe);

  const params = useParams();
  const { push } = useHistory();

  // eslint-disable-next-line max-len
  const recipeData = useMemo(() => data.hits.find((_r, i) => String(i) === params.index),
    [data, params]);

  if (!recipeData) {
    return null;
  }

  const renderIngredient = ({ text, weight, image: ingredientImage }) => (
    <IngredientContainer>
      <IngredientTitle>{`${text}: ${weight}`}</IngredientTitle>
      <IngredientImage src={ingredientImage} />
    </IngredientContainer>
  );

  const { recipe: { label, image, ingredients } } = recipeData;

  return (
    <Container>
      <TitleContainer>
        <GoBackButton onClick={() => {
          push('/');
        }}
        >
          Go Back

        </GoBackButton>
        <Title>{label}</Title>
      </TitleContainer>

      <Image src={image} />
      {ingredients.map(renderIngredient)}
    </Container>
  );
};

const Container = styled.div`
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
`;

const TitleContainer = styled.div`
    display: flex;
    justify-content: center;
    align-items: center;
`;

const Title = styled.h2`
    margin-left: 20px;
`;

const GoBackButton = styled.span`
    cursor: pointer;
`;

const Image = styled.img`
    width: 300px;
    height: 300px;

`;

const IngredientContainer = styled.div`
     width: 150px;
     height: 150px;

`;

const IngredientTitle = styled.h3`

`;

const IngredientImage = styled.img`
width: 50px;
height: 50px;

`;
