import React, { useCallback, useEffect, useState } from 'react';
import styled from 'styled-components';
import { useDispatch, useSelector } from 'react-redux';
import CircleLoader from 'react-spinners/CircleLoader';
import { useLocation, useHistory } from 'react-router-dom';
import qs from 'qs';
import {
  Input, Button,
} from '../components';
import { RecipeItem } from './components';
// http://api.weatherstack.com/current?access_key=c18c9cd0a7159022db85263eea437534&query=New%20York
import { getRecipeRequest, getMoreRecipeRequest } from './actions';
import { logout as logoutAction } from './components/LoginPage/actions';

export function Recipe() {
  const [value, setValue] = useState('');
  const dispatch = useDispatch();

  const location = useLocation();
  const { push } = useHistory();

  const getRecipe = useCallback(() => {
    dispatch(getRecipeRequest(value));
  }, [dispatch, value]);

  const getMoreRecipe = useCallback(() => {
    dispatch(getMoreRecipeRequest());
  }, [dispatch]);

  const logout = useCallback(() => {
    dispatch(logoutAction());
  }, [dispatch]);

  useEffect(() => {
    const params = qs.parse(location.search, { ignoreQueryPrefix: true });
    if (params.q) {
      setValue(params.q);
      getRecipe();
    }
  }, [location, getRecipe]);

  const {
    loading, loadingMore, data, errorMessage,
  } = useSelector((state) => state.recipe);

  return (
    <Container>
      <form onSubmit={(e) => e.preventDefault()}>
        <Input value={value} onChangeText={setValue} />
        <Button onClick={getRecipe}>Search</Button>
        <Button onClick={logout}>Logout</Button>
      </form>
      {errorMessage ? <h1 style={{ color: 'red' }}>{errorMessage}</h1> : null }
      {
            loading ? <CircleLoader /> : null
        }

      {
            data
              ? data.hits.map(({ recipe }, index) => (
                <RecipeItem
                  key={recipe.uri}
                  data={recipe}
                  onClick={() => {
                    push(`/detailsPage/${index}`);
                  }}
                />
              )) : null
        }

      { data ? <Button onClick={getMoreRecipe}>{loadingMore ? 'loading...' : 'Loading more'}</Button> : null}
    </Container>
  );
}

const Container = styled.div`
 display: flex;
 justify-content: center;
 align-items: center;
 flex-direction: column;

`;
