import React, {
  useCallback, createContext, useContext,
} from 'react';
import styled from 'styled-components';
import { useSelector, useDispatch } from 'react-redux';
import {
  // Table, Input, Button, Form, TableRow,
  Header,
  Form,
  TodoItems,
} from '../components';
import {
  addTodoAction, CHANGE_TODO_COMPLETION_STATUS, DELETE_TODO, CHANGE_CURRENT_PAGE,
} from './actions';

export const TestContext = createContext('');

export function useAppContext() {
  const text = useContext(TestContext);

  return text;
}

export function TodoList() {
  const dispatch = useDispatch();

  const currentPage = useSelector((state) => state.todoList.currentPage);
  const todos = useSelector((state) => state.todoList.todos);

  const addTodo = useCallback((inputValue) => {
    dispatch(addTodoAction(inputValue));
  }, [dispatch]);

  const toggleCompletion = useCallback((todoId) => {
    dispatch({ type: CHANGE_TODO_COMPLETION_STATUS, payload: todoId });
  }, [dispatch]);

  const deleteTodo = useCallback((todoId) => {
    dispatch({ type: DELETE_TODO, payload: todoId });
  }, [dispatch]);

  const setCurrentPage = useCallback((nextPage) => {
    dispatch({ type: CHANGE_CURRENT_PAGE, payload: nextPage });
  }, [dispatch]);

  return (
    <TestContext.Provider value="">
      <Container>
        <Header onButtonClick={setCurrentPage} page={currentPage} />
        <Form onSubmit={addTodo} />

        <TodoItems
          data={todos}
          onDelete={deleteTodo}
          onChangeCompletionStatus={toggleCompletion}
        />
      </Container>
    </TestContext.Provider>

  );
}

const Container = styled.div`
     margin: 0 auto;
     text-align: center;
     width: 500px;
     height: 500px;
  `;

