import {
  LOGIN_REQUEST, loginSuccess, loginFailure, BOOTSTRAP_START, bootstrapFinish, LOGOUT,
} from './actions';

export const loginMiddleware = (store) => (next) => (action) => {
  if (action.type === LOGIN_REQUEST) {
    try {
      localStorage.setItem('isAuthorized', action.payload);
      store.dispatch(loginSuccess());
    } catch (_e) {
      store.dispatch(loginFailure());
    }
  }

  next(action);
};

export const bootstrapMiddleware = (store) => (next) => (action) => {
  if (action.type === BOOTSTRAP_START) {
    const isAuthorized = localStorage.getItem('isAuthorized');
    // other actions

    if (isAuthorized) {
      store.dispatch(loginSuccess());
    } else {
      store.dispatch(loginFailure());
    }

    store.dispatch(bootstrapFinish());
  }

  next(action);
};

export const logoutMiddleware = () => (next) => (action) => {
  if (action.type === LOGOUT) {
    localStorage.removeItem('isAuthorized');
  }

  next(action);
};
