import { createStore, applyMiddleware } from 'redux';
import { composeWithDevTools } from 'redux-devtools-extension';
import { rootReducer } from './rootReducer';
import { weatherMiddleware } from '../Weather';
import {
  getRecipeMiddleware, getMoreRecipeMiddleware, loginMiddleware, bootstrapMiddleware,
  logoutMiddleware,
} from '../Recipe';

export const store = createStore(rootReducer, composeWithDevTools(
  applyMiddleware(weatherMiddleware, getRecipeMiddleware, getMoreRecipeMiddleware,
    loginMiddleware, bootstrapMiddleware, logoutMiddleware),

));
