import {
  LOGIN_SUCCESS, LOGIN_FAILURE, BOOTSTRAP_FINISH, LOGOUT,
} from './actions';

const initialState = {
  isAuthorized: false,
  bootsraped: false,
};

export function loginReducer(state = initialState, action) {
  switch (action.type) {
    case LOGIN_SUCCESS: {
      return {
        ...state,
        isAuthorized: true,
      };
    }

    case LOGIN_FAILURE: {
      return {
        ...state,
        isAuthorized: false,
      };
    }

    case BOOTSTRAP_FINISH: {
      return {
        ...state,
        bootsraped: true,
      };
    }

    case LOGOUT: {
      return {
        ...state,
        isAuthorized: false,
      };
    }

    default: return state;
  }
}
