import {
  GET_RECIPE_REQUEST,
  GET_MORE_RECIPE_REQUEST,
  getRecipeFailure,
  getRecipeSuccess,
  getMoreRecipeSuccess,
  getMoreRecipeFailure,
} from './actions';

export const getRecipeMiddleware = (store) => (next) => (action) => {
  if (action.type === GET_RECIPE_REQUEST) {
    fetch(`https://api.edamam.com/search?app_id=f77ee4cf&app_key=ba49fcc3d798971c2cd26dbfe16343e3&q=${action.payload}`)
      .then((res) => res.json())
      .then((response) => {
        store.dispatch(getRecipeSuccess(response));
      })
      .catch((e) => {
        store.dispatch(getRecipeFailure(e.message));
      });
  }
  next(action);
};

export const getMoreRecipeMiddleware = (store) => (next) => (action) => {
  if (action.type === GET_MORE_RECIPE_REQUEST) {
    const { recipe: { data } } = store.getState();

    if (data) {
      const { q, to } = data;
      fetch(`https://api.edamam.com/search?app_id=f77ee4cf&app_key=ba49fcc3d798971c2cd26dbfe16343e3&q=${q}&from=${to}`)
        .then((res) => res.json())
        .then((response) => {
          store.dispatch(getMoreRecipeSuccess(response));
        })
        .catch((e) => {
          store.dispatch(getMoreRecipeFailure(e.message));
        });
    }
  }
  next(action);
};
