import React from 'react';
import styled from 'styled-components';

export function Button({
  onClick, children, type = 'solid', color = '#000', ...restProps
}) {
  return (
    <StyledButton
      customType={type}
      onClick={onClick}
      color={color}
      {...restProps}
    >
      {children}
    </StyledButton>
  );
}

const StyledButton = styled.button` 
    border-radius: 6px;   
    font-size: 16px;
    padding: 10px 15px;
    cursor: pointer;
    border: none;
    color: white;    
    background-color: transparent;

    &:hover {
      opacity: 0.7;
    }

      ${(props) => (props.customType === 'solid'
    ? `
       background-color: ${props.color};    
    ` : `      
       color: ${props.color};
    `)}

    &:focus {
      outline: none;
    }

`;
