import React, { useCallback } from 'react';
import styled from 'styled-components';
import { Button } from '../Button';

export function TodoItem({
  title, isCompleted, id, onChangeCompletionStatus, onDelete,
}) {
  const onCompletionButtonClick = useCallback(() => {
    onChangeCompletionStatus(id);
  }, [id, onChangeCompletionStatus]);

  const onDeleteClick = useCallback(() => {
    onDelete(id);
  }, [onDelete, id]);

  return (
    <Container isCompleted={isCompleted}>
      <Title>{title}</Title>
      <StyledButton onClick={onCompletionButtonClick} color={isCompleted ? 'orange' : 'green'}>{isCompleted ? 'Undo' : 'Mark Done'}</StyledButton>
      <StyledButton onClick={onDeleteClick} color="red">Delete</StyledButton>
    </Container>
  );
}

const Title = styled.p`
    margin: 5px 0 5px;

`;

const StyledButton = styled(Button)`
    margin: 0 20px;
`;

const Container = styled.div`
    width: 600px;
    height: 80px;
    border: 1px solid black;
    border-radius: 8px;
    margin: 10px 0;
    background-color: ${({ isCompleted }) => (isCompleted ? '#34e5eb' : 'white')}
`;
