import React, { useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { Route, Redirect, Switch } from 'react-router-dom';
import { LoginPage, Recipe, DetailsPage } from './Recipe';
import { bootstrapStart } from './Recipe/components/LoginPage/actions';

const PublicRoute = () => (
  <Switch>
    <Route exact path="/" component={Recipe} />
    <Route exact path="/detailsPage/:index" component={DetailsPage} />
    <Redirect to="/" />
  </Switch>
);

function App() {
  const {
    isAuthorized,
    bootsraped,
  } = useSelector((state) => state.login);
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(bootstrapStart());
  }, [dispatch]);

  return bootsraped
    ? (
      <>
        <Route
          render={() => {
            if (isAuthorized) {
              return <PublicRoute />;
            }

            return <Redirect to="/login" />;
          }}
        />
        <Route exact path="/login" component={LoginPage} />

      </>
    ) : null;
}

// life cycles

// 1) render
// 2) re-render / update
// 3) mount

// npx create-react-app react-app

// Re-render
// 1) Internal state change
// 2) Any props change
// 3) Re-render of parent

export default App;
